<template>
  <auth-layout>

    <form class='auth-form' @submit.prevent='authorize()'>

      <div class="material-icons-outlined auth-icon">login</div>
      <h2>АВТОРИЗАЦІЯ</h2>

      <!-- <p class="auth-text" v-if="refresh">
        Щоб продовжити користування сайтом, будь-ласка, перезайдіть до акаунта.
        Це потрібно для підтвердження вашої особи
      </p> -->

      <input-text v-model='username' placeholder="Ваш логін або email" ref='username'/>
      <input-password v-model='password' ref='password'/>

      <div style="text-align:right"><router-link class="link" to='/recall'>Забули пароль?</router-link></div>
      <button type='submit' class='button-plain auth-button'>Вхід до кабінету</button>
      <small>Не зареєстровані? <router-link to='/reg'>Створити акаунт</router-link></small>

    </form>

  </auth-layout>
</template>

<script>
import authLayout from '@/layouts/auth-layout.vue'
import inputText from '@/components/inputs/input-text.vue'
import inputPassword from '@/components/inputs/input-password.vue'
export default {
  components: { authLayout, inputText, inputPassword },
  data() { return {
    username: '',
    password: '',
    refresh: localStorage.getItem('refresh')
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) }
  },
  methods: {
    async authorize() {
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await this.$store.dispatch('login', { 
          username: this.username, 
          password: this.password
        })
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.link {
  font-size: .85em;
}
small {
  font-size: .85em;
  color: gray;
}
</style>
